@use "modules/core";
@use "modules/essentials/helpers";

.soul-empty-state {
  @include core.flexbox-column();
  @include core.flexbox-center-horizontally-and-vertically()
}

.soul-empty-state__heading,
.soul-empty-state__text,
.soul-empty-state__help-text {
  color: core.$soul-color-text-reduced-on-light;
  text-align: center;
  max-width: var(--_soul-empty-state-row-max-width); // to help readability of title and description
}

.soul-empty-state__heading {
  @include core.soul-font-weight-bold();
  font-size: var(--_soul-empty-state-heading-font-size);
  line-height: var(--_soul-empty-state-heading-line-height);
  margin: var(--_soul-empty-state-heading-margin);
}

.soul-empty-state__text {
  font-size: var(--_soul-empty-state-text-font-size);
  line-height: var(--_soul-empty-state-text-line-height);
  margin: var(--_soul-empty-state-text-margin);
}

.soul-empty-state__help-text {
  font-size: var(--_soul-empty-state-help-text-font-size);
  line-height: var(--_soul-empty-state-help-text-line-height);
  margin: var(--_soul-empty-state-help-text-margin);
}

.soul-empty-state__text,
.soul-empty-state__help-text {
  @include core.soul-font-weight-regular();
}

.soul-empty-state__action {
  margin: var(--_soul-empty-state-action-margin);
}

.soul-empty-state__additional-action {
  margin: var(--_soul-empty-state-additional-action-margin);
}

::slotted(soul-icon) {
  --soul-icon-size: var(--_soul-empty-state-illustration-size);
  --soul-icon-color: #{linear-gradient(to bottom, core.$soul-color-illustration-base 20%, core.$soul-color-illustration-dark)};
}

::slotted(soul-file-icon) {
  --soul-file-icon-size: var(--_soul-empty-state-illustration-size);
  filter: linear-gradient(to bottom, core.$soul-color-illustration-base 20%, core.$soul-color-illustration-dark);
}

::slotted([slot="illustration"])  {
  max-height: var(--_soul-empty-state-illustration-max-height);
  max-width: var(--_soul-empty-state-illustration-max-width);
}

:host,
:host([size="m"]) {
  display: block;
  --_soul-empty-state-heading-font-size: #{core.get-font-size(core.$soul-l)};
  --_soul-empty-state-heading-line-height: #{core.get-line-height(core.$soul-l)};
  --_soul-empty-state-heading-margin: #{core.$soul-space-stack-top-xs};
  --_soul-empty-state-text-font-size: #{core.get-font-size(core.$soul-m)};
  --_soul-empty-state-text-line-height: #{core.get-line-height(core.$soul-m)};
  --_soul-empty-state-text-margin: #{core.$soul-space-stack-top-2-xs};
  --_soul-empty-state-help-text-font-size: #{core.get-font-size(core.$soul-xs)};
  --_soul-empty-state-help-text-line-height: #{core.get-line-height(core.$soul-xs)};
  --_soul-empty-state-help-text-margin: #{core.$soul-space-stack-top-m};
  --_soul-empty-state-row-max-width: 45ch;
  --_soul-empty-state-illustration-size: #{core.$soul-space-unit-3-xl + core.$soul-space-unit-m};
  --_soul-empty-state-action-margin: #{ core.$soul-space-stack-top-m};
  --_soul-empty-state-additional-action-margin: #{ core.$soul-space-stack-top-s};
  --_soul-empty-state-illustration-max-height: #{core.$soul-space-unit-3-xl + core.$soul-space-unit-3-xl};
  --_soul-empty-state-illustration-max-width: #{(core.$soul-space-unit-3-xl + core.$soul-space-unit-2-xl) * 2};

}

:host([size="s"]) {
  --_soul-empty-state-heading-font-size: #{core.get-font-size(core.$soul-s)};
  --_soul-empty-state-heading-line-height: #{core.get-line-height(core.$soul-s)};
  --_soul-empty-state-heading-margin: #{core.$soul-space-stack-top-2-xs};
  --_soul-empty-state-text-font-size: #{core.get-font-size(core.$soul-s)};
  --_soul-empty-state-text-line-height: #{core.get-line-height(core.$soul-s)};
  --_soul-empty-state-text-margin: #{core.$soul-space-stack-top-3-xs};
  --_soul-empty-state-help-text-font-size: #{core.get-font-size(core.$soul-xs)};
  --_soul-empty-state-help-text-line-height: #{core.get-line-height(core.$soul-xs)};
  --_soul-empty-state-help-text-margin: #{core.$soul-space-stack-top-xs};
  --_soul-empty-state-row-max-width: 45ch;
  --_soul-empty-state-illustration-size: #{core.$soul-space-unit-2-xl};
  --_soul-empty-state-action-margin: #{ core.$soul-space-stack-top-xs};
  --_soul-empty-state-additional-action-margin: #{ core.$soul-space-stack-top-2-xs};
  --_soul-empty-state-illustration-max-height: #{core.$soul-space-unit-2-xl + core.$soul-space-unit-2-xl};
  --_soul-empty-state-illustration-max-width: #{(core.$soul-space-unit-2-xl + core.$soul-space-unit-xl) * 2};
}
